import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import QueryBuilder from 'react-querybuilder'
import { formatQuery } from 'react-querybuilder';
// import 'react-querybuilder/dist/query-builder.scss';


function createFilds(columns: string[]) : any[] {
  return columns.map(col => {
    return {
      name: col,
      label: col
    }
  })
}



class StreamlitQueryBuilder extends StreamlitComponentBase {

  public render = (): ReactNode => {

    const fields = createFilds(this.props.args["fields"])

    return (
      <QueryBuilder fields={fields} onQueryChange={this.logQuery} />
    )
  }

  private logQuery = (query: any): void => {
      Streamlit.setComponentValue(formatQuery(query, 'sql'))
  }

  /** Focus handler for our "Click Me!" button. */
  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  /** Blur handler for our "Click Me!" button. */
  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(StreamlitQueryBuilder)
